






import Vue from 'vue'
import WindowFactory from '@/components/window/window-factory'
export default Vue.extend({
    props:{
        name:{
            type:String,
            required:true
        }
    },
    computed:{
        icon:function(){
            return { maskImage: "url('"+require(`../${this.name}/icon.svg`)+"')" }
        }
    },
    methods:{
        openSetting:function() {
            WindowFactory.OpenSetting(this.$props.name);
        }
    }
})
